.fg {
  position: relative;
  margin: 0;
  input {
    border: 1px solid $secondary;
    background: rgba(217, 217, 217, 0.07);
    padding: 0 1.28em;
    height: 3em;
    width: 100%;
    font-size: 1em;
    color: $textColor;
    &::placeholder {
      color: #DBDBDB;
    }
    &:focus {
      &::placeholder {
        color: rgba(#000, 0.3);
      }
    }
  }
}

@media screen and (max-width: 576px){
  .fg{
    input{
      font-size: .875em;
    }
  }
}
