.header{
  padding: 2.3125em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 8.625em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu{
  display: flex;
  align-items: center;
  &__buttons{
    display: flex;
    align-items: center;
    margin-left: 6em;
  }
  &__btn{
    width: 2.125em;
    height: 2.125em;
    svg{
      width: 100%;
      height: auto;
    }
    &:not(:last-child){
      margin-right: 1em;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    &:not(:last-child){
      margin-right: 3.75em;
    }
    span{
      font-size: .875em;
      line-height: 140%;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.6875em 0;
    &__logo{
      width: 4.6875em;
    }
    &._open{
      .burger{
        background-image: url(../img/burger2.svg);
      }
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .header__logo,
  .burger{
    position: relative;
    z-index: 31;
  }

  .burger{
    display: flex;
    width: 2em;
    height: 2em;
    @include bgImgSize(100%);
    background-image: url(../img/burger.svg);
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    background-color: #fff;
    z-index: 30;
    display: block;
    padding: 7.5em 1.25em 4em;
    overflow-y: auto;
    &__buttons{
      margin: 3em 0 0;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      &:not(:last-child){
        margin: 0 0 1.25em;
      }
      span{
        font-size: 1.25em;
      }
    }
  }
}
