.main{
  overflow: hidden;
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: 40em calc(100% - 40em);
  }
  &__content{
    padding-bottom: 3em;
  }
  &__img{
    align-self: flex-end;
    img{
      width: 126%;
      display: block;
      margin-left: -4em;
    }
    ._view2{
      display: none;
    }
  }
  &__info{
    margin-top: .875em;
  }
  &__desc{
    margin-top: 1.5em;
    @include grid100gap(1.5em);
    max-width: 23.125em;
  }
  &__nav{
    display: flex;
    margin-top: 4em;
  }
  &__hint{
    margin-top: 4.25em;
    @include grid100gap(.0625em);
    li{
      font-size: .625em;
      &:before{
        background: $gray;
      }
    }
  }
  .swiper{
    &-slide{
      overflow: hidden;
    }
    &-pagination{
      width: 50%;
      bottom: 8.375em;
      left: 0;
      z-index: 2;
      display: flex;
      justify-content: flex-end;
    }
  }
}

.advantages{
  padding: 5.5em 0 6.625em;
  background: $blueBg;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 2em 3em;
    padding-left: 3.75em;
    margin-top: 5.375em;
  }
  &__nav{
    padding-right: 3.75em;
    padding-bottom: .625em;
    grid-column: span 2;
    align-self: flex-end;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__hint{
    max-width: 15em;
    margin-left: 5em;
    opacity: 0.5;
  }
}

.equip{
  padding-top: 2.75em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 46.5em) 42.5em;
    gap: 4em;
  }
  &__img{
    img{
      margin-left: -50%;
      width: 150%;
      display: block;
    }
  }
}

.equip-doc{
  display: flex;
  align-items: center;
  margin-top: 2em;
  &__img{
    display: block;
    width: 6em;
    margin-right: 4em;
    flex-shrink: 0;
    img{
      width: 100%;
    }
  }
  &__text{
    max-width: 19.375em;
    @include grid100gap(1.5em);
  }
}

.worker{
  padding: 3.3125em 0 5.75em;
  background-color: $secondary;
  color: #fff;
  &__title{
    text-align: right;
  }
  &__grid{
    display: flex;
    justify-content: space-between;
    margin-top: 1em;
  }
}

.services{
  padding: 3.25em 0 2.125em;
  &__title{
    text-align: center;
  }
  &__tabs{
    margin-top: 3.375em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .125em));
    gap: .25em;
  }
  &__groups{
    @include grid100gap(.25em);
    &:not(:last-child){
      margin-bottom: 1.875em;
    }
  }
  &__subtitle{
    text-align: center;
    margin-bottom: 1em;
  }
  .tab{
    background: rgba(217, 217, 217, 0.2);
    @include center;
    height: 10.5em;
    color: #08D6C8;
    transition: all .3s;
    cursor: pointer;
    img{
      width: 8.75em;
      display: block;
      flex-shrink: 0;
      margin-right: .25em;
    }
    &._active{
      background-color: $primary;
      color: #fff;
    }
  }
}

.tab{
  &-content{
    display: none;
    &._active{
      display: block;
    }
  }
}

.quotes{
  margin-top: 5.875em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1em));
  gap: 2em;
  &__item{
    display: flex;
    justify-content: center;
  }
  &__img{
    width: 6.5625em;
    display: block;
    margin-top: -1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    margin-left: 2em;
    padding-left: 2.75em;
    min-height: 8.375em;
    display: flex;
    align-items: center;
    border-left: 3px solid $primary;
    max-width: 20em;
  }
}

.promo{
  padding: 2.125em 0 3.4375em;
  &__title{
    text-align: center;
  }
  &__grid{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
  }
}

.services2{
  padding: 3.25em 0;
  background: $blueBg;
  &__grid{
    margin-top: 3.875em;
    display: flex;
    justify-content: space-between;
  }
  .services-card2{
    width: 31%;
  }
}

.consult{
  padding: 7em 0 5.625em;
  background: rgba(217, 217, 217, 0.16);
  &__grid{
    position: relative;
  }
  &__content{
    max-width: 46.875em;
    min-height: 27.375em;
  }
  &__img{
    width: 37.125em;
    position: absolute;
    right: 0;
    top: -4em;
    img{
      width: 100%;
      display: block;
      mix-blend-mode: multiply;
    }
  }
  &__text{
    max-width: 25em;
    margin-top: 5em;
    @include grid100gap(.875em);
    ul{
      li{
        font-size: .875em;
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: 4em;
  }
}

.result{
  padding: 4.875em 0 4.125em;
  &__content{
    max-width: 43.75em;
  }
  &__info{
    margin-top: 1em;
    @include grid100gap(3.5em);
  }
  &__subtitle{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 700;
    font-family: $font2;
    color: $secondary;
  }
  &__text{
    p{
      font-size: 1em;
      line-height: 112.5%;
      margin-bottom: .5em;
    }
    .m-list{
      padding-left: .625em;
    }
  }
  &__grid{
    display: flex;
    margin-top: 8.75em;
    padding-left: 7.5em;
    .result-card{
      &:nth-child(1){
        width: 30.3125em;
        ._round1{
          top: -5.75em;
          left: 2.625em;
        }
        ._round2{
          left: -6em;
          top: 4.62em;
        }
        ._round3{
          bottom: 1em;
          left: -3.25em;
        }
      }
      &:nth-child(2){
        margin-left: -1.6875em;
        margin-top: -7em;
        width: 22.1875em;
        ._round1{
          top: -2.125em;
          right: 100%;
          margin-right: -3em;
        }
        ._round2{
          right: -8.5em;
          top: -1.125em;
        }
      }
      &:nth-child(3){
        width: 17.875em;
        margin-left: -10.5em;
        margin-top: -25.8125em;
        ._round1{
          top: 4.375em;
          left: -9.75em;
        }
      }
    }
  }
}

.result-card{
  position: relative;
  &__img{
    img{
      width: 100%;
    }
  }
  &__round{
    z-index: -1;
    position: absolute;
    width: 11.5em;
    height: 11.5em;
    border-radius: 50%;
    @include center;
    text-align: center;
    padding: 1em;
    background-color: $primary;
    color: #fff;
    &._small{
      width: 8.875em;
      height: 8.875em;
    }
  }
}

.process{
  padding: 4.875em 0 3em;
  //background: $blueBg;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: auto;
    opacity: 0.8;
  }
  &__tabs{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .tab{
    padding: 0 4.5em 0 6em;
    color: $secondary;
    border: 1px solid $secondary;
    height: 4.375em;
    display: flex;
    align-items: center;
    border-radius: 3.75em;
    position: relative;
    cursor: pointer;
    transition: all .3s;
    &__num{
      position: absolute;
      width: 6.375em;
      height: 6.375em;
      @include center;
      background-color: $primary;
      border-radius: 50%;
      left: -2.5em;
      top: 50%;
      transform: translateY(-50%);
      font-weight: 700;
      font-family: $font2;
      color: #fff;
      span{
        font-size: 4em;
      }
    }
    &__title{
      font-size: 1.25em;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $secondary;
    }
  }
  .tab-content{
    margin-top: 5.25em;
    max-width: 38.75em;
  }
  &__text{
    ul{
      @include grid100gap(.5em);
    }
    p{
      font-size: 1em;
      line-height: 112.5%;
      margin-bottom: 2em;
    }
  }
  &__nav{
    margin-top: 4.5em;
    display: flex;
  }
  &__btn{
    width: 25em;
    max-width: 100%;
  }
}

.about{
  padding: 4.625em 0 4em;
  &__slider{
    margin: 3.25em auto 0;
    max-width: 1290px;
    overflow: hidden;
  }
  .swiper{
    padding: 0 4.375em;
    &-wrapper{
      align-items: center;
      min-height: 26.7em;
    }

    &-slide{
      transition: all .3s;
      width: 17em;
      opacity: .3;
      position: relative;
      @media screen and (min-width: 993px){
        &-next{
          opacity: .7;
          width: 20.375em;
          transform: translateX(-6.6875em);
          &+.swiper-slide{
            width: 22.875em;
            opacity: 1;
            transform: translateX(calc(-6.6875em - 2.9375em));
            z-index: 3;
            &+.swiper-slide{
              opacity: .7;
              width: 20.375em;
              transform: translateX(calc(-6.6875em - 2.9375em - 2.9375em));
              &+.swiper-slide{
                opacity: .3;
                width: 17em;
                transform: translateX(calc(-6.6875em - 2.9375em - 2.9375em - 4.25em));
              }
            }
          }
        }
      }
    }
    &-pagination{
      position: static;
      margin-top: 3.625em;
    }
  }
  &__img{
    @include imgCover(116%);
  }
}

.review{
  background: rgba(#D9D9D9,.16);
  padding: 4em 0;
  overflow: hidden;
  &__slider{
    margin-top: 3.5em;
    position: relative;
  }
  .swiper{
    &-container{
      width: calc(100% + 7em);
      overflow: hidden;
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 7em;
    }
    &-button{
      width: 1.6875em;
      img{
        width: 100%;
        display: block;
      }
      &-prev{
        left: -3em;
      }
      &-next{
        right: -3em;
      }
    }
  }
}

.contacts{
  padding-top: 5em;
  overflow: hidden;
  &__inner{
    position: relative;
  }
  &__img{
    position: absolute;
    right: -9.25em;
    bottom: 0;
    width: 43.75em;
  }
  &__content{
    padding-bottom: 3.3125em;
    max-width: 46em;
  }
  &__socials{
    margin-top: 4.25em;
    display: flex;
    align-items: center;
  }
  &__social{
    width: 6em;
    margin-right: 3em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      .fill-color{
        fill: $secondary;
      }
    }
  }
  &__hint{
    margin-top: 3.5625em;
    max-width: 33.75em;
    li{
      font-size: .875em;
      color: #9EA8A8;
      &:before{
        background: #9EA8A8;
      }
    }
  }
}

.faq{
  padding: 7em 0;
  background: rgba(#D9D9D9,.16);
  &__items{
    margin-top: 6em;
    @include grid100gap(2.75em);
  }
}

.why{
  background-color: $primary;
  &__inner{
    padding: 4em 0 18.25em;
    position: relative;
  }
  &__img{
    width: 64.1875em;
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
  }
  &__content{
    max-width: 31.25em;
  }
  &__title{
    color: #fff;
  }
  &__round{
    position: absolute;
    width: 10.5em;
    height: 10.5em;
    border-radius: 50%;
    background: rgba(240, 255, 254, 0.64);
    @include center;
    text-align: center;
    padding: .5em;
    span{
      font-size: 1.25em;
      line-height: 120%;
    }
  }
  ._round1{
    left: 0;
    bottom: .875em;
    width: 11.5em;
    height: 11.5em;
  }
  ._round2{
    left: 13em;
    bottom: 7.875em;
  }
  ._round3{
    bottom: 3.5em;
    left: 27.875em;
  }
  ._round4{
    width: 14.625em;
    height: 14.625em;
    bottom: 16.0625em;
    left: 33.8125em;
  }
  ._round5{
    width: 12.75em;
    height: 12.75em;
    right: 0;
    bottom: 3em;
  }
}

.order{
  padding: 10em 0;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 2em;
  }
}

.order-form{
  margin-top: 2.25em;
  @include grid100gap(1.875em);
  max-width: 30em;
  .menu__buttons{
    margin: 1em 0 0;
    justify-content: center;
  }
}

.map{
  height: 22.875em;
  min-height: 100%;
  width: 100%;
}



@media screen and (max-width: 992px){
  .main{
    &__inner{
      grid-template-columns: 100%;
      position: relative;
    }
    &__content{
      padding-bottom: .6875em;
      max-width: 85%;
      margin-left: auto;
    }
    &__img{
      position: absolute;
      bottom: -4em;
      left: -8.5em;
      z-index: -1;
      width: 28.5em;
      img{
        width: 100%;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .text48,
    .text40,
    &__desc{
      text-align: right;
    }
    &__info{
      margin-top: .25em;
    }
    &__desc{
      margin-top: .625em;
      margin-left: auto;
      gap: 1.375em;
      max-width: 13.75em;
    }
    &__nav{
      margin-top: 2em;
      justify-content: flex-end;
    }
    &__hint{
      margin-top: 1.25em;
      margin-left: -1.25em;
      width: calc(100% + 1.25em);
      gap: .25em;
    }
    .swiper{
      &-pagination{
        display: none;
      }
    }
  }

  .advantages{
    padding: 3em 0 7.5em;
    &__title{
      text-align: center;
    }
    &__grid{
      margin-top: 3.625em;
      padding: 0;
      grid-template-columns: repeat(2,calc(50% - 1.125em));
      gap: 2.25em;
    }
    &__nav{
      grid-column: 1/-1;
    }
  }

  .equip{
    padding-bottom: 3.125em;
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__img{
      img{
        margin: 0;
        width: 100%;
      }
    }
  }

  .worker{
    padding: 2.375em 0 4.875em;
    &__title{
      text-align: left;
    }
  }

  .services{
    padding: 2.8125em 0 4.375em;
    &__tabs{
      margin-top: 2.75em;
    }
  }

  .quotes{
    grid-template-columns: 100%;
    gap: 1em;
    margin-top: 3em;
    &__item{
      display: block;
      &:nth-child(2){
        display: none;
      }
    }
    &__img{
      width: 2.5em;
      margin: 0 0 1.375em 1.0625em;
    }
    &__text{
      margin: 0 0 0 2em;
    }
  }

  .promo{
    padding-bottom: 3.75em;
    &__grid{
      margin-top: 2.625em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 2em 1.25em;
    }
  }

  .consult{
    padding: 3.5em 0 2.625em;
    &__text{
      margin-top: 2em;
    }
    &__img{
      position: static;
      margin: -30% 0 0 auto;
      max-width: 100%;
      width: 25em;
    }
    &__nav{
      justify-content: center;
      margin-top: 1em;
    }
  }

  .result{
    &__grid{
      flex-direction: column;
      padding: 0;
      margin-top: 7.5em;
    }
    &__info{
      margin-top: .625em;
      gap: 3.25em;
    }
    &__text{
      max-width: 15em;
    }
    .result-card{
      &:nth-child(1){
        width: 13.75em;
        margin-left: 3.75em;
        ._round1{
          top: -5.25em;
          right: -2.5em;
          left: unset;
        }
        ._round2{
          top: 6em;
          left: -4.75em;
          width: 7.5em;
          height: 7.5em;
          z-index: 2;
        }
        ._round3{
          z-index: 2;
          left: unset;
          top: unset;
          right: -.75em;
          bottom: -3.625em;
        }
      }
      &:nth-child(2){
        margin: .625em 0 0 3em;
        order: 2;
        width: 23.75em;
        ._round1{
          width: 7em;
          height: 7em;
          top: unset;
          bottom: -1.5em;
          left: -1.5em;
        }
        ._round2{
          display: none;
        }
      }
      &:nth-child(3){
        width: 17.5em;
        margin: .875em 0 0;
        position: relative;
        z-index: 2;
        ._round1{
          bottom: -7.1875em;
          left: 2em;
          top: unset;
          z-index: 2;
        }
      }
    }
  }

  .result-card{
    &__round{
      width: 9.375em;
      height: 9.375em;
      &._small{
        width: 7.5em;
        height: 7.5em;
      }
    }
  }

  .process{
    padding: 4.25em 0 3.5em;
    overflow: hidden;
    &__tabs{
      @include grid100gap(2.375em);
      padding-left: 2em;
    }
    .tab{
      height: 3.5em;
      padding: 0 0 0 2em;
      text-align: center;
      justify-content: center;
      &__title{
        font-size: 1em;
      }
      &__num{
        width: 5.25em;
        height: 5.25em;
        left: -2em;
        span{
          font-size: 3em;
        }
      }
    }
    .tab-content{
      margin-top: 3.75em;
    }
    &__nav{
      margin-top: 6.25em;
    }
    &__img{
      height: 22.875em;
      bottom: 7.875em;
      top: unset;
      right: -4em;
    }
  }

  .about{
    padding: 2.5em 0;
    &__title{
      text-align: center;
    }
    &__slider{
      margin-top: 3.375em;
      padding: 0 4.375em;
    }
    .swiper{
      &-container{
        transform: translateX(2.5em);
      }
      &-wrapper{
        min-height: 45em;
      }
      &-slide{
        width: 89%;
        opacity: .7;
        &-active{
          width: 100%;
          opacity: 1;
        }
      }
      &-pagination{
        margin-top: 2.625em;
      }
    }
  }

  .review{
    padding: 1.5em 0 2.75em;
    &__slider{
      margin-top: 3.875em;
      padding: 0 2.5em;
    }
    .swiper{
      &-container{
        width: calc(100% + 3em);
      }
      &-slide{
        width: 50%;
        padding-right: 3em;
      }
      &-button{
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
    }
  }

  .contacts{
    padding: 2.75em 0 0;
    &__socials{
      margin-top: 2.5em;
    }
    &__social{
      width: 4em;
      margin-right: 1.5em;
    }
    &__hint{
      margin-top: 2.125em;
      max-width: 12.75em;
      li{
        font-size: .75em;
      }
    }
    &__img{
      position: static;
      width: 100%;
      display: block;
      margin-left: auto;
      margin-right: -1.25em;
      margin-top: -7em;
    }
  }

  .faq{
    padding: 4.125em 0 4.75em;
    &__items{
      gap: 1.5em;
      margin-top: 3em;
    }
  }

  .why{
    &__inner{
      padding: 2.25em 0 0;
    }
    &__info{
      position: relative;
    }
    &__img{
      position: static;
      width: 100%;
      display: block;
    }
    &__round{
      span{
        font-size: .875em;
      }
    }
    ._round1{
      width: 8.125em;
      height: 8.125em;
      left: .625em;
      bottom: 8.5em;
    }
    ._round2{
      left: 1.875em;
      bottom: 1em;
      width: 5.75em;
      height: 5.75em;
    }
    ._round3{
      bottom: 13em;
      left: 10em;
      width: 4.875em;
      height: 4.875em;
    }
    ._round4{
      width: 6.875em;
      height: 6.875em;
      left: unset;
      right: 11.1875em;
      bottom: 18.625em;
    }
    ._round5{
      width: 9.375em;
      height: 9.375em;
      right: 0;
      bottom: 1.375em;
    }
  }

  .order{
    padding: 3.75em 0 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
  }

  .map{
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__grid{
      grid-template-columns: 100%;
    }
    &__nav{
      flex-direction: column;
      align-items: flex-start;
    }
    &__hint{
      margin: 1.25em 0 0;
    }
  }

  .order,
  .why,
  .faq,
  .contacts,
  .review,
  .about,
  .result,
  .equip,
  .worker,
  .services,
  .promo,
  .services2,
  .consult{
    &__title{
      font-size: 2em;
    }
  }

  .equip{
    position: relative;
    padding-top: 8.75em;
    &__img{
      position: absolute;
      height: 20em;
      right: -3.1875em;
      top: -9em;
      img{
        height: 100%;
        width: auto;
      }
    }
  }

  .equip-doc{
    &__img{
      width: 6.75em;
      margin-right: .75em;
    }
  }

  .worker{
    &__grid{
      @include grid100gap(2em);
    }
  }

  .services{
    ._type2{
      .service-card{
        &__col{
          &:nth-child(1){
            grid-template-columns: 100%;
            text-align: center;
          }
        }
      }
    }
    .tab{
      padding: 0 1.25em;
      position: relative;
      height: 7.3125em;
      img{
        width: 4.875em;
        position: absolute;
        margin: 0;
        top: 0;
        right: .875em;
      }
      span{
        font-size: 1.5em;
      }
    }
  }

  .promo{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .services2{
    &__grid{
      @include grid100gap(3.5em);
    }
    .services-card2{
      width: 100%;
      margin: 0 auto;
    }
  }

  .consult{
    overflow: hidden;
    &__text{
      max-width: 13em;
    }
    &__img{
      width: 100%;
      margin-top: -45%;
      img{
        width: 150%;
        margin-left: -25%;
      }
    }
    &__btn{
      height: 4.5625em;
      text-align: center;
      span{
        font-size: 1em;
      }
    }
  }

  .process{
    &__nav{
      justify-content: center;
    }
    &__btn{
      width: 14.25em;
      height: 4.375em;
      span{
        font-size: 1em;
      }
    }
  }

  .about{
    .swiper{
      &-wrapper{
        min-height: 16em;
      }
    }
  }

  .review{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .contacts{
    &__img{
      margin-right: -6.5em;
    }
  }

  .why{
    &__info{
      margin-top: 4.875em;
    }
    ._round3{
      left: unset;
      right: 6.5em;
    }
    ._round4{
      right: 2.1875em;
    }
    ._round5{
      right: -1.25em;
    }
    &__img{
      height: 18.9375em;
      width: auto;
      margin-left: -8.5em;
    }
  }

  .order-form{
    &__btn{
      height: 4.375em;
      width: 14.25em;
      text-align: center;
      margin: 0 auto;
      span{
        font-size: 1em;
      }
    }
  }

  .map{
    height: 14.375em;
  }
}
