.text48,
.text40,
.text32{
  font-family: $font2;
  font-weight: 700;
  line-height: 127%;
}

.text48{
  font-size: 3em;
}

.text40{
  font-size: 2.5em;
}

.text32{
  font-size: 2em;
}

.text16{
  font-size: 1em;
  line-height: 114%;
}

.text14{
  font-size: .875em;
  line-height: 135%;
}

.text10{
  font-size: .625em;
  line-height: 130%;
}

.m-list{
  li{
    font-size: 1em;
    line-height: 112.5%;
    position: relative;
    padding-left: .75em;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: .25em;
      width: .25em;
      height: .25em;
      border-radius: 50%;
      background-color: $textColor;
    }
  }
}

.primary-text{
  color: $primary;
}

.secondary-text{
  color: $secondary;
}

.gray-text{
  color: $gray;
}


@media screen and (max-width: 992px){
  .text48{
    font-size: 2.25em;
  }

  .text40{
    font-size: 1.5em;
  }
}
