@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 3em;
    height: 3.5625em;
    border-radius: 3.75em;
    font-size: 1em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
        line-height: 140%;
    }
}

.m-btn-primary{
    color: #fff;
    background-color: $primary;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
    }
}

.m-btn-secondary{
    color: #fff;
    background-color: $secondary;
    &:hover{
        color: #fff;
        background-color: darken($secondary,10%);
    }
}
