@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper-pagination{
  &-bullet{
    width: .9375em;
    height: .9375em;
    border-radius: 50%;
    opacity: 1;
    background: #D9D9D9;
    margin: 0 .375em !important;
    &:hover,
    &-active{
      background: $primary;
    }
  }
}

.slideToggle-block {
  &__hidden {
    height: 0;
    overflow: hidden;
    transition: all .425s;
  }
}

.scroll-up {
  z-index: 22;
  position: fixed;
  right:  3.125em;
  bottom:  .5em;
  width: 3.125em;
  height: 3.125em;
  transition: all .3s;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #fff;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  box-shadow: 0 5px 12px rgba(51, 51, 51, .1);
  &:hover {
    background-color: $primary;
    border-color: $primary;
    path {
      stroke: $textColor
    }
  }
  svg{
    width: 1.5em;
    height: auto;
  }
  @media screen and (max-width: 992px) {
    right: 1.25em;
    bottom: 1.25em;
    width: 2.5em;
    height: 2.5em;
    svg {
      width: 1.125em;
    }
  }
  &--active {
    visibility: visible;
    opacity: 1;
  }
}



@media screen and (max-width: 992px){
  .swiper-pagination{
    &-bullet{
      width: .6875em;
      height: .6875em;
      margin: 0 .25em !important;
    }
  }
}
