.advantage-card{
  position: relative;
  display: flex;
  align-items: center;
  &:after{
    content: '';
    position: absolute;
    top: -.625em;
    left: -.625em;
    width: 3.625em;
    height: 3.625em;
    border-radius: 50%;
    background-color: $primary;
  }
  &:before{
    content: '';
    width: 7.875em;
    height: 7.875em;
    border-radius: 50%;
    background: #FFFFFF;
    flex-shrink: 0;
  }
  &__text{
    margin-left: -4.375em;
    max-width: 17.125em;
    position: relative;
    z-index: 3;
  }
}

.worker-card{
  text-align: center;
  max-width: 28.125em;
  width: 45%;
  &__img{
    width: 25.625em;
    max-width: 100%;
    margin: 0 auto 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__name{
    font-weight: 700;
    font-family: $font2;
  }
  &__name,
  &__desc{
    font-size: 1.25em;
    line-height: 125%;
  }
  &__desc{
    margin-top: .7em;
  }
}

.service-card{
  padding: 1.5em 0;
  background: rgba(8, 214, 201, 0.14);
  &__inner{
    margin: 0 auto;
    max-width: 1440px;
    padding: 0 5em;
    display: grid;
    grid-template-columns: 12.5em 12.5em 12.5em 12.5em 12.5em calc(100% - 65em);
    gap: .5em;
  }
  &__col{
    &:nth-child(1){
      align-self: center;
    }
  }
  &__items{
    margin-top: 1.25em;
    span{
      font-size: .875em;
      line-height: 120%;
      display: block;
    }
  }
  &__subtitle{
    text-align: center;
    max-width: 11em;
    margin: 0 auto;
  }
  &__price{
    margin-top: auto;
    padding-top: 1.25em;
    min-height: 3em;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    text-align: right;
    font-weight: 700;
    font-family: $font2;
    &-old{
      font-size: 1em;
      line-height: 125%;
      color: #ABC4C2;
      text-decoration: line-through;
    }
    &-new{
      margin-top: .1875em;
      font-size: 1.5em;
      line-height: 125%;
      color: #08D6C8;
    }
  }
  &__info{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1.375em 0;
    min-height: 11em;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}

.promo-card{
  text-align: center;
  padding-top: 8.75em;
  position: relative;
  &__img{
    position: absolute;
    height: 13.75em;
    width: auto;
    top: 0;
    left: -2.5em;
    z-index: 2;
  }
  &__round{
    padding-bottom: 100%;
    background-color: $secondary;
    border-radius: 50%;
    position: relative;
    margin-bottom: 1.25em;
  }
  &__title{
    padding: .5em;
    @include centerAbsolute;
    max-width: 100%;
    width: 100%;
    font-size: 1.5em;
    line-height: 130%;
    font-weight: 700;
    font-family: $font2;
    color: #fff;
  }
}

.services-card2{
  max-width: 17.6875em;
  text-align: center;
  &__img{
    margin-bottom: 1.25em;
    @include imgCover(100%);
    img{
      border-radius: 50%;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 130%;
    font-weight: 700;
    font-family: $font2;
    color: $primary;
  }
}

.review-card{
  &__top{
    padding: 0 1.875em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 10.25em;
      height: 10.25em;
      border-radius: 50%;
      z-index: -1;
      top: 1em;
      right: -.625em;
      background-color: $primary;
    }
  }
  &__img{
    @include imgCover(100%);
    img{
      border-radius: 50%;
      display: block;
    }
  }
  &__content{
    padding: .875em 0 .875em 2.5em;
    border-left: 3px solid $primary;
    @include grid100gap(2.5em);
    margin-top: 3em;
  }
  &__name{
    font-size: 1.25em;
    line-height: 140%;
    font-family: $font2;
    font-weight: 700;
    color: $primary;
  }
}

.qa-card{
  &.slideToggle-block--active{
    .qa-card__btn{
      background-image: url(../img/minus.svg);
    }
  }
  &__hidden{
    //display: none;
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-weight: 700;
    font-family: $font2;
    font-size: 1.5em;
    line-height: 120%;
  }
  &__btn{
    width: 2.25em;
    height: 2.25em;
    flex-shrink: 0;
    margin-left: 1em;
    background-image: url(../img/plus.svg);
    @include bgImgSize(100%);
  }
  .slideToggle-block__wrap{
    padding-top: 2em;
    max-width: 58em;
  }
  &__text{
    @include grid100gap(1em);
    p{
      line-height: inherit;
    }
  }
}



@media screen and (max-width: 992px){
  .worker-card{
    &__img{
      margin-bottom: 1.375em;
    }
    &__desc{
      font-size: 1em;
      margin-top: 1em;
    }
  }

  .service-card{
    padding: 0;
    &__inner{
      padding: 0;
      grid-template-columns: 100%;
    }
    &__col{
      padding: 1em 2.5em;
      display: grid;
      grid-template-columns: repeat(2,50%);
      align-items: center;
      &:nth-child(1){
        background: #fff;
        grid-template-columns: calc(100% - 8em) 7.5em;
        gap: .5em;
      }
    }
    &__info{
      min-height: unset;
    }
    &__subtitle{
      text-align: left;
      margin: 0;
    }
    &__price{
      padding: 0 0 0 .5em;
      margin: 0;
    }
    &__nav{
      padding: 1.875em 0 1.5em;
      justify-content: center;
    }
  }

  .promo-card{
    padding-top: 6.5em;
    &__round{
      width: 12.5em;
      height: 12.5em;
      padding: 0;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: .875em;
    }
    &__img{
      height: 10em;
      left: 50%;
      transform: translateX(-50%);
      margin-left: -2.75em;
    }
    &__title{
      font-size: 1.25em;
    }
  }

  .services-card2{
    max-width: 15.375em;
    &__title{
      font-size: 1.25em;
    }
  }

  .review-card{
    &__content{
      padding-left: 2em;
      margin-top: 1.875em;
      gap: 1.375em;
    }
    .text16{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .worker-card{
    width: 100%;
    max-width: 100%;
    &__img{
      width: 17.5em;
    }
  }

  .review-card{
    &__top{
      padding-left: 0;
      padding-right: 2.375em;
    }
  }
}
